.aboutContainer {
    display: flex;
    height: calc(100vh - var(--headerHeight));
    /* padding: 0 5vw 0 5vw; */
    align-items: center;
    justify-content: center;
}

.aboutDetails {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 35%;
}

.aboutTitle {
    font-weight: bolder;
    font-size: 3vw;
    padding-bottom: 20px;
}

.aboutText {
    line-height: 1.5rem;
}