nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: var(--headerHeight);
    background-color: gray;
    color: white;
}

.nav-link {
    width: 40%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style: none;
}

.logoContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 10vw;
}