.homeContainer {
    display: flex;
    height: calc(100vh - var(--headerHeight));
    padding: 0 5vw 0 5vw;
    align-items: center;
}

.homeArticle {
    display: flex;
    align-items: flex-start;
    text-align: start;
    flex-direction: column;
    width: 40%;
}

.articleTitle {
    font-weight: bolder;
    font-size: 3vw;
    margin-bottom: 20px;
}


/* 5898514c905442dc21e2eea0fd6a905e1cb69b73 */

.articleText {
    line-height: 1.5rem;
    margin-bottom: 20px;
}

.articleBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 110px;
    height: 50px;
    border-radius: 10px;
    background-color: gray;
}